import { useState, useEffect, useCallback, useRef } from 'react';
import { 
  Card, 
  Input, 
  Button, 
  Checkbox, 
  Space, 
  Typography, 
  Alert,
  message,
  Tooltip,
} from 'antd';
import { 
  SendOutlined, 
  RobotOutlined,
  ApiOutlined,
} from '@ant-design/icons';
import ReactMarkdown from 'react-markdown';
import { useApp } from '../contexts/AppContext';
import { ACTION_TYPES } from '../contexts/AppContext';
import apiService from '../services/apiService';
import { setupErrorSuppression } from '../utils/safeFetch';
import { 
  PageHeader, 
  StatusAlert, 
  ExecutionPlan
} from '../components';

const { TextArea } = Input;
const { Text, Paragraph } = Typography;

const AIAgentPage = () => {
  const { state, dispatch } = useApp();
  const [request, setRequest] = useState('');
  const [dryRun, setDryRun] = useState(true);
  const [autoScroll, setAutoScroll] = useState(true); // Enable auto-scroll by default
  const [executionTimer, setExecutionTimer] = useState(null);
  const [backendAvailable, setBackendAvailable] = useState(true); // Track backend connection status
  
  // Recovery state - SIMPLIFIED to bare minimum
  const [isRecoveryAnalyzing, setIsRecoveryAnalyzing] = useState(false); // Show "Analyzing..." loading indicator
  const [recoveryExecutionId, setRecoveryExecutionId] = useState(null); // Store executionId for recovery confirmation
  const [originalExecutionPlan, setOriginalExecutionPlan] = useState(null); // Store original plan for cancel button only
  const [recoveryMetadata, setRecoveryMetadata] = useState(null); // Store recovery plan metadata (preservedCount, newStepsCount, etc.)
  
  // Ref for execution plan section to enable smooth scrolling
  const executionPlanRef = useRef(null);
  // Ref for ExecutionPlan component to access scrollToStep method
  const executionPlanComponentRef = useRef(null);

  // Check backend health on component mount and when WebSocket connects
  useEffect(() => {
    // Setup global error suppression for fetch errors
    const cleanup = setupErrorSuppression();
    
    // Check WebSocket connection status to determine backend availability
    const checkWebSocketConnection = () => {
      if (state.websocket) {
        const isConnected = state.websocket.readyState === WebSocket.OPEN;
        setBackendAvailable(isConnected);
        if (isConnected) {
          console.log('‚úÖ Backend available (WebSocket connected)');
        } else {
          console.log('‚ö†Ô∏è Backend unavailable (WebSocket not connected)');
        }
      } else {
        setBackendAvailable(false);
        console.log('‚ö†Ô∏è Backend unavailable (No WebSocket)');
      }
    };
    
    // Check immediately
    checkWebSocketConnection();
    
    // Add event listeners to detect connection state changes
    if (state.websocket) {
      const ws = state.websocket;
      
      const handleOpen = () => {
        console.log('‚úÖ WebSocket opened - Backend available');
        setBackendAvailable(true);
      };
      
      const handleClose = () => {
        console.log('‚ö†Ô∏è WebSocket closed - Backend unavailable');
        setBackendAvailable(false);
      };
      
      const handleError = () => {
        console.log('‚ùå WebSocket error - Backend unavailable');
        setBackendAvailable(false);
      };
      
      ws.addEventListener('open', handleOpen);
      ws.addEventListener('close', handleClose);
      ws.addEventListener('error', handleError);
      
      return () => {
        ws.removeEventListener('open', handleOpen);
        ws.removeEventListener('close', handleClose);
        ws.removeEventListener('error', handleError);
        cleanup(); // Remove error suppression listeners
      };
    }
    
    return () => {
      cleanup(); // Remove error suppression listeners
    };
  }, [state.websocket]); // Re-check when WebSocket changes

  // Start execution timer
  const startExecutionTimer = useCallback(() => {
    const startTime = new Date();
    
    const timer = setInterval(() => {
      const now = new Date();
      const elapsed = Math.floor((now - startTime) / 1000);
      const minutes = Math.floor(elapsed / 60);
      const seconds = elapsed % 60;
      const timeString = `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
      
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_ELAPSED,
        payload: timeString,
      });
    }, 1000);
    
    setExecutionTimer(timer);
  }, [dispatch]);

  // Stop execution timer
  const stopExecutionTimer = useCallback(() => {
    if (executionTimer) {
      clearInterval(executionTimer);
      setExecutionTimer(null);
    }
  }, [executionTimer]);

  // Helper function to update step status
  const updateStepStatus = useCallback((stepId, status) => {
    dispatch({
      type: ACTION_TYPES.UPDATE_STEP_STATUS,
      payload: { stepId, status }
    });
  }, [dispatch]);

  // Helper function to update execution progress
  const updateExecutionProgress = useCallback(() => {
    dispatch({
      type: ACTION_TYPES.UPDATE_EXECUTION_PROGRESS
    });
  }, [dispatch]);

  // Helper function to update execution log
  const updateExecutionLog = useCallback((logData) => {
    dispatch({
      type: ACTION_TYPES.ADD_AGENT_EXECUTION_LOG,
      payload: {
        ...logData,
        timestamp: new Date(),
      }
    });
  }, [dispatch]);

  // SIMPLIFIED: When recovery plan is received, just REPLACE the execution plan
  // The plan becomes "pending" and waits for user confirmation like the original plan
  const showRecoveryPlan = useCallback((data) => {
    console.log('üìã Recovery plan received, replacing current execution plan');
    
    const { executionId, strategy, failureContext } = data;
    
    // Detailed validation with helpful error messages
    if (!strategy) {
      message.error('Backend sent incomplete recovery plan (missing strategy field)');
      setIsRecoveryAnalyzing(false);
      return;
    }
    
    if (!strategy.executionPlan) {
      message.error('Backend sent incomplete recovery plan (missing execution plan)');
      setIsRecoveryAnalyzing(false);
      return;
    }
    
    if (strategy.executionPlan.length === 0) {
      message.error('Recovery plan has no steps');
      setIsRecoveryAnalyzing(false);
      return;
    }
    
    // Store original plan ONCE (only on first recovery) for cancel functionality
    if (!originalExecutionPlan) {
      console.log('üíæ Storing original plan for cancel button');
      setOriginalExecutionPlan(state.agentExecutionPlan);
    }
    
    // Store executionId for when user confirms
    setRecoveryExecutionId(executionId);
    console.log('üíæ Set recoveryExecutionId:', executionId);
    
    // Store recovery metadata for UI display
    setRecoveryMetadata({
      preservedCount: strategy.preservedCount || 0,
      newStepsCount: strategy.newStepsCount || 0,
      totalSteps: strategy.totalSteps || strategy.executionPlan.length,
      recoveryNotes: strategy.recoveryNotes || '',
      reasoning: strategy.reasoning || '',
      successProbability: strategy.successProbability || 0,
      riskLevel: strategy.riskLevel || 'unknown',
      estimatedDuration: strategy.estimatedDuration || 'unknown',
      attemptNumber: failureContext?.attemptNumber || 1,
    });
    
    // Convert recovery steps to same format as original plan
    // CRITICAL: Preserve the status from backend! Completed steps stay 'completed'
    // Mark recovery steps for special styling
    const preservedCount = strategy.preservedCount || 0;
    const newStepsCount = strategy.newStepsCount || 0;
    
    const recoverySteps = strategy.executionPlan.map((step, index) => {
      const isRecoveryStep = index >= preservedCount && index < (preservedCount + newStepsCount);
      
      return {
        id: step.id,
        name: step.name,
        action: step.action,
        resource_type: step.resourceId || step.mcpTool || 'recovery',
        description: step.description,
        status: step.status || 'pending',  // ‚Üê PRESERVE backend status!
        mcpTool: step.mcpTool,
        toolParameters: step.toolParameters,
        parameters: step.parameters,
        dependsOn: step.dependsOn,
        estimatedDuration: step.estimatedDuration,
        riskLevel: step.riskLevel,
        isRecoveryStep: isRecoveryStep,  // Mark recovery steps for styling
      };
    });
    
    // REPLACE the execution plan (recovery plan becomes THE plan)
    dispatch({
      type: ACTION_TYPES.SET_AGENT_EXECUTION_PLAN,
      payload: recoverySteps,
    });
    
    // Set status to 'pending' - this will show the confirm button
    dispatch({
      type: ACTION_TYPES.SET_CURRENT_EXECUTION,
      payload: { status: 'pending', executionId: executionId, isRecovery: true },
    });
    
    // Stop showing "Analyzing..." indicator
    setIsRecoveryAnalyzing(false);
    
    const attemptMsg = failureContext?.attemptNumber > 1 
      ? ` (Recovery Attempt #${failureContext.attemptNumber})` 
      : '';
    message.info(`Recovery plan ready - please review and confirm${attemptMsg}`);
  }, [dispatch, state.agentExecutionPlan, originalExecutionPlan]);

  // SIMPLIFIED: Confirm recovery - send WebSocket approval and update UI state
  const handleConfirmRecovery = useCallback(async () => {
    console.log('üîµ ========================================');
    console.log('üîµ Sending WebSocket approval to backend');
    console.log('üîµ ========================================');
    
    if (!recoveryExecutionId) {
      message.error('No recovery plan to confirm');
      return;
    }

    // Show loading state immediately
    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'agent', value: true },
    });

    try {
      // Clear logs for fresh execution
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_LOGS,
        payload: [],
      });
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_PROGRESS,
        payload: 0,
      });
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
        payload: false,
      });
      
      // Reset step statuses (only pending steps will be executed)
      if (state.agentExecutionPlan) {
        const resetPlan = state.agentExecutionPlan.map(step => {
          // Keep completed steps as completed, reset pending steps
          if (step.status === 'completed') {
            return step;
          }
          return { ...step, status: 'pending' };
        });
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_PLAN,
          payload: resetPlan,
        });
      }
      
      // Send WebSocket approval to backend
      if (state.websocket && state.websocket.readyState === WebSocket.OPEN) {
        state.websocket.send(JSON.stringify({
          type: 'plan_recovery_decision',
          executionId: recoveryExecutionId,
          approved: true,
          timestamp: new Date().toISOString()
        }));
        
        message.success('Recovery plan confirmed - execution starting...');
        
        // Scroll to execution plan to show progress
        setTimeout(() => {
          if (executionPlanRef.current) {
            executionPlanRef.current.scrollIntoView({ 
              behavior: 'smooth', 
              block: 'start',
              inline: 'nearest'
            });
          }
        }, 200);
      } else {
        throw new Error('WebSocket not connected');
      }
    } catch (error) {
      console.error('‚ùå Failed to send recovery approval:', error);
      message.error('Failed to send recovery confirmation');
      
      // Clear loading state on error
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'agent', value: false },
      });
    }
  }, [recoveryExecutionId, state.websocket, state.agentExecutionPlan, dispatch, executionPlanRef]);

  // SIMPLIFIED: Cancel recovery - restore original plan and abort
  const handleCancelRecovery = useCallback(async () => {
    console.log('‚ùå User cancelled recovery - restoring original plan');
    
    if (!recoveryExecutionId) {
      return;
    }

    try {
      // Send abort via WebSocket
      if (state.websocket && state.websocket.readyState === WebSocket.OPEN) {
        state.websocket.send(JSON.stringify({
          type: 'plan_recovery_abort',
          executionId: recoveryExecutionId,
          approved: false,
          timestamp: new Date().toISOString()
        }));
      }
    } catch (error) {
      console.error('Failed to send recovery abort:', error);
    }

    // Restore original plan if available
    if (originalExecutionPlan) {
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_PLAN,
        payload: originalExecutionPlan,
      });
    }

    // Clean up recovery state
    setRecoveryExecutionId(null);
    setOriginalExecutionPlan(null);
    
    // Update execution status
    dispatch({
      type: ACTION_TYPES.UPDATE_EXECUTION_STATUS,
      payload: 'aborted',
    });
    
    stopExecutionTimer();
    message.info('Recovery cancelled');
  }, [recoveryExecutionId, originalExecutionPlan, state.websocket, dispatch, stopExecutionTimer]);

  // AI Agent specific WebSocket message handler
  const handleAIAgentWebSocketMessage = useCallback((data) => {
    // Log ALL WebSocket messages for debugging
    console.log('üì® WebSocket message received:', {
      type: data.type,
      executionId: data.executionId,
      hasStrategy: !!data.strategy,
      hasFailureContext: !!data.failureContext,
      keys: Object.keys(data)
    });
    
    switch (data.type) {
      case 'processing_started':
        dispatch({
          type: ACTION_TYPES.SET_STATUS_MESSAGE,
          payload: `Processing: ${data.request}`,
        });
        dispatch({
          type: ACTION_TYPES.SET_LOADING,
          payload: { key: 'agent', value: true },
        });
        break;
      
      case 'processing_completed':
        if (data.success) {
          dispatch({
            type: ACTION_TYPES.SET_STATUS_MESSAGE,
            payload: 'Processing completed successfully',
          });
        } else {
          dispatch({
            type: ACTION_TYPES.SET_STATUS_MESSAGE,
            payload: 'Processing failed',
          });
        }
        dispatch({
          type: ACTION_TYPES.SET_LOADING,
          payload: { key: 'agent', value: false },
        });
        break;
      
      case 'execution_started':
        startExecutionTimer();
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
          payload: false,
        });
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_PROGRESS,
          payload: 0,
        });
        updateExecutionLog({
          type: 'info',
          message: data.message || 'Execution started',
          stepId: null, // global log
        });
        break;
        
      case 'step_started':
        console.log('üîµ Step started:', { stepId: data.stepId, message: data.message });
        updateStepStatus(data.stepId, 'running');
        updateExecutionLog({
          type: 'info',
          message: data.message,
          stepId: data.stepId,
        });
        
        // Auto-scroll to the step that just started (if enabled)
        if (autoScroll) {
          setTimeout(() => {
            if (executionPlanComponentRef.current && data.stepId) {
              executionPlanComponentRef.current.scrollToStep(data.stepId);
            }
          }, 100);
        }
        break;
      
      case 'step_progress':
        console.log('üîµ Step progress:', { stepId: data.stepId, message: data.message });
        updateExecutionLog({
          type: 'info',
          message: data.message,
          stepId: data.stepId,
        });
        break;
      
      case 'step_completed':
        console.log('‚úÖ Step completed:', { stepId: data.stepId, message: data.message });
        updateStepStatus(data.stepId, 'completed');
        updateExecutionProgress();
        updateExecutionLog({
          type: 'success',
          message: data.message,
          stepId: data.stepId,
        });
        
        // Auto-scroll to the completed step (if enabled)
        if (autoScroll) {
          setTimeout(() => {
            if (executionPlanComponentRef.current && data.stepId) {
              executionPlanComponentRef.current.scrollToStep(data.stepId);
            }
          }, 100);
        }
        break;
      
      case 'step_failed':
        console.log('‚ùå Step failed:', { stepId: data.stepId, message: data.message });
        updateStepStatus(data.stepId, 'failed');
        updateExecutionLog({
          type: 'error',
          message: data.message,
          stepId: data.stepId,
        });
        
        // Auto-scroll to the failed step (if enabled)
        if (autoScroll) {
          setTimeout(() => {
            if (executionPlanComponentRef.current && data.stepId) {
              executionPlanComponentRef.current.scrollToStep(data.stepId);
            }
          }, 100);
        }
        break;
      
      case 'execution_completed':
        console.log('‚úÖ Execution completed successfully');
        stopExecutionTimer();
        
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_PROGRESS,
          payload: 100,
        });
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
          payload: true,
        });
        dispatch({
          type: ACTION_TYPES.UPDATE_EXECUTION_STATUS,
          payload: 'completed',
        });
        
        // Clean up ALL recovery state
        setOriginalExecutionPlan(null);
        setRecoveryExecutionId(null);
        setIsRecoveryAnalyzing(false);
        setRecoveryMetadata(null);
        
        message.success('Execution completed successfully');
        
        // Auto-refresh infrastructure state
        setTimeout(() => {
          dispatch({ type: ACTION_TYPES.REFRESH_INFRASTRUCTURE });
        }, 1000);
        break;
      
      case 'execution_aborted':
        console.log('‚ùå Execution aborted');
        stopExecutionTimer();
        
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
          payload: true,
        });
        dispatch({
          type: ACTION_TYPES.UPDATE_EXECUTION_STATUS,
          payload: 'aborted',
        });
        
        // Clean up ALL recovery state
        setOriginalExecutionPlan(null);
        setRecoveryExecutionId(null);
        setIsRecoveryAnalyzing(false);
        setRecoveryMetadata(null);
        
        message.error('Execution was aborted');
        break;
      
      // Plan-level recovery messages
      case 'plan_recovery_analyzing':
        setIsRecoveryAnalyzing(true); // Show loading state
        updateExecutionLog({
          type: 'warning',
          message: data.message || `Plan failed at step ${data.failedStepIndex + 1}. AI is analyzing recovery options...`,
          stepId: data.failedStepId,
        });
        break;
        
      case 'plan_recovery_request':
        // Show recovery plan as next steps for user confirmation
        showRecoveryPlan(data);
        break;
        
      case 'plan_recovery_executing':
        console.log('‚úÖ Backend started executing recovery plan');
        console.log('üìã Recovery execution data:', {
          executionId: data.executionId,
          action: data.action,
          totalSteps: data.totalSteps
        });
        
        setIsRecoveryAnalyzing(false);
        
        // Clear loading state
        dispatch({
          type: ACTION_TYPES.SET_LOADING,
          payload: { key: 'agent', value: false },
        });
        
        // Start execution timer
        startExecutionTimer();
        
        // IMPORTANT: Update currentExecution with recovery executionId from backend
        // This ensures step updates will match the correct execution
        dispatch({
          type: ACTION_TYPES.SET_CURRENT_EXECUTION,
          payload: { 
            status: 'running', 
            executionId: data.executionId,
            isRecovery: true 
          },
        });
        
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
          payload: false,
        });
        
        // Clear recovery executionId (we don't need it anymore)
        setRecoveryExecutionId(null);
        
        message.success('Executing recovery plan...');
        
        updateExecutionLog({
          type: 'info',
          message: `Recovery plan execution started: ${data.action || 'Executing recovery steps'}`,
          stepId: null,
        });
        break;
        
      case 'plan_recovery_completed':
        console.log('‚úÖ Recovery completed successfully');
        setIsRecoveryAnalyzing(false);
        message.success('Recovery completed - continuing execution');
        break;
        
      case 'plan_recovery_failed':
        console.error('‚ùå Recovery failed');
        setIsRecoveryAnalyzing(false);
        stopExecutionTimer();
        
        dispatch({
          type: ACTION_TYPES.UPDATE_EXECUTION_STATUS,
          payload: 'failed',
        });
        
        setRecoveryExecutionId(null);
        message.error('Recovery failed');
        break;
      
      default:
        break;
    }
  }, [dispatch, startExecutionTimer, stopExecutionTimer, updateStepStatus, updateExecutionProgress, updateExecutionLog, showRecoveryPlan, autoScroll, executionPlanComponentRef]);

  // Setup direct WebSocket message handler
  useEffect(() => {
    const { websocket } = state;
    if (!websocket) {
      return;
    }
    
    const messageHandler = (event) => {
      try {
        const data = JSON.parse(event.data);
        handleAIAgentWebSocketMessage(data);
      } catch (error) {
        console.error('AIAgentPage: Failed to parse WebSocket message:', error);
      }
    };

    websocket.addEventListener('message', messageHandler);

    // Cleanup when component unmounts or WebSocket changes
    return () => {
      websocket.removeEventListener('message', messageHandler);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [state.websocket, handleAIAgentWebSocketMessage]);

  // Cleanup timer on unmount
  useEffect(() => {
    return () => {
      if (executionTimer) {
        clearInterval(executionTimer);
      }
    };
  }, [executionTimer]);

  const handleProcessRequest = async () => {
    if (!request.trim()) {
      message.warning('Please enter a request');
      return;
    }

    // Check backend availability before processing
    if (!backendAvailable) {
      message.error({
        content: (
          <div>
            <div style={{ fontWeight: 600, marginBottom: 4 }}>Backend Server Not Available</div>
            <div style={{ fontSize: 13 }}>
              The backend server (http://localhost:8080) is not responding.
            </div>
            <div style={{ fontSize: 12, marginTop: 8, color: '#666' }}>
              Please ensure the backend server is running and try again.
            </div>
          </div>
        ),
        duration: 6,
      });
      
      return;
    }

    console.log('üÜï New request submitted - resetting ALL state');
    
    // RESET ALL STATE when starting new request
    dispatch({
      type: ACTION_TYPES.CLEAR_AGENT_RESPONSE,
    });
    stopExecutionTimer();
    
    // Clear ALL recovery state
    setOriginalExecutionPlan(null);
    setRecoveryExecutionId(null);
    setIsRecoveryAnalyzing(false);
    setRecoveryMetadata(null);

    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'agent', value: true },
    });

    try {
      const response = await apiService.processAgentRequest(request, dryRun);
      
      dispatch({
        type: ACTION_TYPES.SET_AGENT_RESPONSE,
        payload: response,
      });
      
      // If there's an execution plan, display it
      if (response.executionPlan && response.executionPlan.length > 0) {
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_PLAN,
          payload: response.executionPlan,
        });
      }
      
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: 'Request processed successfully',
      });
      
      // Backend is working, ensure status is updated
      setBackendAvailable(true);
    } catch (error) {
      console.error('Agent request failed:', error);
      
      // Clear any previous response on error
      dispatch({
        type: ACTION_TYPES.CLEAR_AGENT_RESPONSE,
      });
      
      // Provide user-friendly error messages
      let errorMessage = 'Failed to process request';
      let isConnectionError = false;
      
      if (error.status === 500) {
        errorMessage = 'Server error occurred. Please try again or check with your administrator.';
      } else if (error.status === 400) {
        errorMessage = error.message || 'Invalid request. Please check your input and try again.';
      } else if (error.status === 401) {
        errorMessage = 'Authentication required. Please log in and try again.';
      } else if (error.status === 403) {
        errorMessage = 'You do not have permission to perform this action.';
      } else if (error.status === 404) {
        errorMessage = 'Service not found. Please check your configuration.';
      } else if (error.status === 429) {
        errorMessage = 'Too many requests. Please wait a moment and try again.';
      } else if (error.message && error.message !== 'Failed to fetch') {
        errorMessage = error.message;
      } else if (error.message === 'Failed to fetch') {
        isConnectionError = true;
        errorMessage = 'Unable to connect to backend server. The server may not be running.';
        setBackendAvailable(false); // Update backend status
      }
      
      if (isConnectionError) {
        message.error({
          content: (
            <div>
              <div style={{ fontWeight: 600, marginBottom: 4 }}>Connection Failed</div>
              <div style={{ fontSize: 13 }}>{errorMessage}</div>
              <div style={{ fontSize: 12, marginTop: 8, color: '#666' }}>
                Make sure the backend server is running on port 8080 and try again.
              </div>
            </div>
          ),
          duration: 6,
        });
      } else {
        message.error(errorMessage);
      }
      
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: `Error: ${errorMessage}`,
      });
    } finally {
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'agent', value: false },
      });
    }
  };

  const handleConfirmExecution = async () => {
    console.log('üü¢ ========================================');
    console.log('üü¢ ORIGINAL PLAN CONFIRMATION');
    console.log('üü¢ User clicked "Confirm & Execute" on ORIGINAL plan');
    console.log('üü¢ This WILL call API: /agent/execute-with-plan-recovery');
    console.log('üü¢ ========================================');
    
    if (!state.agentResponse?.decision?.id) {
      message.error('No execution plan to confirm');
      return;
    }

    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'agent', value: true },
    });

    try {
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_LOGS,
        payload: [],
      });
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_PROGRESS,
        payload: 0,
      });
      dispatch({
        type: ACTION_TYPES.SET_AGENT_EXECUTION_COMPLETED,
        payload: false,
      });
      stopExecutionTimer(); // Clear any existing timer
      
      // Reset all step statuses to pending
      if (state.agentExecutionPlan) {
        const resetPlan = state.agentExecutionPlan.map(step => ({ ...step, status: 'pending' }));
        dispatch({
          type: ACTION_TYPES.SET_AGENT_EXECUTION_PLAN,
          payload: resetPlan,
        });
      }
      
      // Pass dry_run flag from stored agentResponse or local state
      const isDryRun = state.agentResponse?.dry_run ?? dryRun;
      await apiService.executeConfirmedPlan(state.agentResponse.decision.id, isDryRun);
      
      dispatch({
        type: ACTION_TYPES.SET_CURRENT_EXECUTION,
        payload: { status: 'running', decisionId: state.agentResponse.decision.id },
      });
      
      // Show different message based on mode
      if (isDryRun) {
        message.success('Dry run execution started');
      } else {
        message.success('Execution started');
      }
      
      // Scroll to execution plan section after state updates
      setTimeout(() => {
        if (executionPlanRef.current) {
          executionPlanRef.current.scrollIntoView({ 
            behavior: 'smooth', 
            block: 'start',
            inline: 'nearest'
          });
        }
      }, 200);
    } catch (error) {
      console.error('Execution start failed:', error);
      
      // Provide user-friendly error messages
      let errorMessage = 'Failed to start execution';
      
      if (error.status === 500) {
        errorMessage = 'Server error occurred while starting execution. Please try again.';
      } else if (error.status === 400) {
        errorMessage = error.message || 'Invalid execution request. Please regenerate the plan and try again.';
      } else if (error.status === 404) {
        errorMessage = 'Execution plan not found. Please regenerate the plan and try again.';
      } else if (error.status === 409) {
        errorMessage = 'Another execution is already in progress. Please wait for it to complete.';
      } else if (error.message && error.message !== 'Failed to fetch') {
        errorMessage = error.message;
      } else if (error.message === 'Failed to fetch') {
        errorMessage = 'Unable to connect to server. Please check your connection and try again.';
      }
      
      message.error(errorMessage);
      
      dispatch({
        type: ACTION_TYPES.SET_STATUS_MESSAGE,
        payload: `Error: ${errorMessage}`,
      });
    } finally {
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'agent', value: false },
      });
    }
  };

  const handleCancelPlan = () => {
    dispatch({
      type: ACTION_TYPES.CLEAR_AGENT_RESPONSE,
    });
    stopExecutionTimer();
    message.info('Plan cancelled');
  };

  const handleKeyPress = (e) => {
    if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
      handleProcessRequest();
    }
  };

  const renderExecutionPlan = () => {
    const logs = Array.isArray(state.agentExecutionLogs) ? state.agentExecutionLogs : [];
    
    // SIMPLIFIED: Determine if recovery based on multiple signals
    // 1. We have a recoveryExecutionId (recovery plan waiting for confirmation)
    // 2. OR currentExecution has isRecovery flag (backend set it)
    const isRecoveryPlan = !!recoveryExecutionId || state.currentExecution?.isRecovery;
    
    console.log('üîç renderExecutionPlan check:', {
      recoveryExecutionId,
      currentExecutionIsRecovery: state.currentExecution?.isRecovery,
      isRecoveryPlan,
      currentExecutionStatus: state.currentExecution?.status
    });
    
    const planTitle = isRecoveryPlan 
      ? 'Recovery Plan' 
      : (dryRun ? 'Execution Plan (Dry Run)' : 'Execution Plan');
    const buttonText = isRecoveryPlan
      ? 'Confirm Recovery'
      : (dryRun ? 'Confirm & Execute (Dry Run)' : 'Confirm & Execute');
    
    // SIMPLIFIED: Just check if currently executing
    const isCurrentlyExecuting = state.currentExecution?.status === 'running';
    
    return (
      <div 
        ref={executionPlanRef}
        style={{ 
          scrollMarginTop: '100px',
        }}
      >
        {/* Recovery Plan Summary - show above execution plan */}
        {isRecoveryPlan && recoveryMetadata && (
          <Card 
            style={{ 
              marginTop: 16, 
              marginBottom: 8,
              background: '#fff7e6',
              border: '1px solid #ffa940'
            }}
          >
            <Space direction="vertical" size="small" style={{ width: '100%' }}>
              <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                <span style={{ fontSize: 16, fontWeight: 600 }}>üîß Recovery Strategy</span>
              </div>
              
              {recoveryMetadata.reasoning && (
                <Text style={{ fontSize: 13, color: '#595959' }}>
                  {recoveryMetadata.reasoning}
                </Text>
              )}
              
              <div style={{ 
                display: 'flex', 
                gap: 16, 
                flexWrap: 'wrap',
                marginTop: 8,
                padding: '8px 12px',
                background: '#fff',
                borderRadius: 4,
                border: '1px solid #ffd591'
              }}>
                <Text style={{ fontSize: 12 }}>
                  <strong>Completed:</strong> {recoveryMetadata.preservedCount} steps ‚úì
                </Text>
                <Text style={{ fontSize: 12 }}>
                  <strong>Recovery:</strong> {recoveryMetadata.newStepsCount} new steps üîß
                </Text>
                <Text style={{ fontSize: 12 }}>
                  <strong>Total:</strong> {recoveryMetadata.totalSteps} steps
                </Text>
                <Text style={{ fontSize: 12 }}>
                  <strong>Success:</strong> {Math.round((recoveryMetadata.successProbability || 0) * 100)}%
                </Text>
                <Text style={{ fontSize: 12 }}>
                  <strong>Risk:</strong> {recoveryMetadata.riskLevel}
                </Text>
                <Text style={{ fontSize: 12 }}>
                  <strong>Duration:</strong> ~{recoveryMetadata.estimatedDuration}
                </Text>
              </div>
              
              {recoveryMetadata.recoveryNotes && (
                <Alert
                  message={recoveryMetadata.recoveryNotes}
                  type="info"
                  showIcon
                  style={{ marginTop: 8, fontSize: 12 }}
                />
              )}
            </Space>
          </Card>
        )}
        
        <ExecutionPlan
          ref={executionPlanComponentRef}
          steps={state.agentExecutionPlan}
          logs={logs}
          isExecuting={isCurrentlyExecuting}
          executionProgress={state.agentExecutionProgress}
          executionElapsed={state.agentExecutionElapsed}
          executionCompleted={state.agentExecutionCompleted}
          onConfirm={isRecoveryPlan ? handleConfirmRecovery : handleConfirmExecution}
          onCancel={isRecoveryPlan ? handleCancelRecovery : handleCancelPlan}
          showConfirmButton={true}
          showCancelButton={true}
          confirmButtonText={buttonText}
          title={planTitle}
          isRecoveryAnalyzing={isRecoveryAnalyzing}
        />
      </div>
    );
  };

  const renderAgentResponse = () => {
    if (!state.agentResponse || state.loading.agent) return null;

    return (
      <Card title={<><RobotOutlined /> AI Agent Response</>} style={{ marginTop: 16 }}>
        <Space direction="vertical" size="middle" style={{ width: '100%' }}>
          <Alert
            message="Request Processed"
            description={
              <div>
                <Paragraph><strong>Request:</strong> {state.agentResponse.request}</Paragraph>
                <Paragraph><strong>Mode:</strong> {state.agentResponse.mode === 'demo' ? 'Demo Mode' : (state.agentResponse.dry_run ? 'Dry Run' : 'Live Execution')}</Paragraph>
                <Paragraph><strong>Confidence:</strong> {(state.agentResponse.confidence * 100).toFixed(1)}%</Paragraph>
              </div>
            }
            type="info"
            showIcon
          />

          {state.agentResponse.mode === 'demo' && (
            <Card size="small" title="Response">
              <Text>{state.agentResponse.response}</Text>
            </Card>
          )}

          {state.agentResponse.mode === 'live' && (
            <>
              <Card size="small" title="Action">
                <Text>{state.agentResponse.action}</Text>
              </Card>
              
              <Card size="small" title="Reasoning">
                <div className="reasoning-content">
                  <ReactMarkdown>{state.agentResponse.reasoning}</ReactMarkdown>
                </div>
              </Card>
            </>
          )}

          {state.agentExecutionPlan && renderExecutionPlan()}
        </Space>
      </Card>
    );
  };

  return (
    <div style={{ maxWidth: 1200, margin: '0 auto' }}>
      <PageHeader
        title="Single Prompt Execute"
        description="Describe what you want to do with your infrastructure in natural language."
      />

      {/* Status Message - hide when backend is offline */}
      {backendAvailable && (
        <StatusAlert 
          message={state.statusMessage} 
          type={state.statusMessage?.startsWith('Error:') ? 'error' : undefined}
          showIcon={state.statusMessage?.startsWith('Error:')}
        />
      )}

      {/* Backend Offline Warning */}
      {!backendAvailable && (
        <Alert
          message="Backend Server Not Available"
          description={
            <div>
              <p style={{ marginBottom: 8 }}>
                The backend server (http://localhost:8080) is not responding. 
                Please start the backend server to use this application.
              </p>
              <div style={{ fontSize: 12, color: '#666' }}>
                <strong>To start the backend:</strong>
                <ol style={{ marginTop: 8, marginBottom: 8, paddingLeft: 20 }}>
                  <li>Navigate to your backend project directory</li>
                  <li>Run: <code style={{ background: '#f5f5f5', padding: '2px 6px' }}>go run cmd/server/main.go</code></li>
                  <li>Wait for "Server starting on :8080" message</li>
                  <li>The connection will automatically reconnect</li>
                </ol>
              </div>
            </div>
          }
          type="warning"
          showIcon
          icon={<ApiOutlined />}
          style={{ marginBottom: 16 }}
        />
      )}

      <Card>
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
          <TextArea
            placeholder="Example: Create a new VPC with 2 subnets and an auto-scaling group with 3 EC2 instances running nginx"
            value={request}
            onChange={(e) => setRequest(e.target.value)}
            onKeyDown={handleKeyPress}
            rows={4}
            style={{ fontSize: 16 }}
          />
          
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Space direction="horizontal" size="large">
              <Checkbox 
                checked={dryRun} 
                onChange={(e) => setDryRun(e.target.checked)}
              >
                Dry Run Mode
              </Checkbox>
              
              <Checkbox 
                checked={autoScroll} 
                onChange={(e) => setAutoScroll(e.target.checked)}
              >
                Auto-scroll to steps
              </Checkbox>
              
            </Space>
            
            <Tooltip 
              title={
                !backendAvailable 
                  ? 'Backend server is not available. Please start it first.' 
                  : !request.trim() 
                  ? 'Please enter a request' 
                  : 'Click to process your request'
              }
            >
              <Button
                type="primary"
                size="large"
                icon={<SendOutlined />}
                onClick={handleProcessRequest}
                loading={state.loading.agent}
                disabled={!request.trim() || !backendAvailable}
                danger={!backendAvailable}
              >
                Process Request
              </Button>
            </Tooltip>
          </div>
          
          <div style={{ fontSize: 12, color: '#666' }}>
            Tip: Press Ctrl+Enter (Cmd+Enter on Mac) to quickly process your request
          </div>
        </Space>
      </Card>

      {renderAgentResponse()}

    </div>
  );
};

export default AIAgentPage;